import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { 
    Navbar,
    Nav,
    NavDropdown,
    Jumbotron,
    Button
} from 'react-bootstrap';

class Navs extends Component {
    render() {
        return (
            <div>
                <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
                    <Navbar.Brand href="#home">React PWA</Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                      <Nav className="mr-auto">
                        <Nav.Link> 
                            <Link to="/" style={{ color: 'white' }}> 
                                Home 
                            </Link> 
                        </Nav.Link>
                        <Nav.Link> 
                            <Link to="/catalog" style={{ color: 'white' }}> 
                                Catalog 
                            </Link> 
                        </Nav.Link>
                        <Nav.Link> 
                            <Link to="/warung" style={{ color: 'white' }}> 
                                Warung 
                            </Link> 
                        </Nav.Link>
                        <Nav.Link> 
                            <Link to="/nested" style={{ color: 'white' }}> 
                                Nested 
                            </Link> 
                        </Nav.Link>
                        <NavDropdown title="Dropdown" id="collasible-nav-dropdown" style={{ color: 'white' }}>
                          <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                          <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                          <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                          <NavDropdown.Divider />
                          <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                        </NavDropdown>
                      </Nav>
                      <Nav>
                        <Nav.Link href="#deets">Sign In</Nav.Link>
                        <Nav.Link eventKey={2} href="#memes">
                          Sign Up
                        </Nav.Link>
                      </Nav>
                    </Navbar.Collapse>
                </Navbar>
                <Jumbotron>
                  <h1>Hello, world!</h1>
                  <p>
                    This is a simple hero unit, a simple jumbotron-style component for calling
                    extra attention to featured content or information.
                  </p>
                  <p>
                    <Button variant="primary">Learn more</Button>
                  </p>
                </Jumbotron>
            </div>
        )
    }
};

export default Navs;