export const CatalogView = () => {
    // const { classes } = props;
    return(
        <React.Fragment>
        <div style={{ marginRight: '5%', marginLeft: '5%' }}>
            <h3> Catalog Produk </h3>
            <main>
                <div>
                    {/* End hero unit */}
                    <Grid container spacing={40}>
                        { products.map((product) =>
                            <Grid item key={product.id} sm={6} md={4} lg={4}>
                            <Card>
                              <CardMedia
                                style={{ paddingTop: '56.25%' }}
                                image={product.image}
                                title="Image title"
                              />
                              <CardContent>
                                <Typography gutterBottom variant="h5" component="h2">
                                  {product.label}
                                </Typography>
                                <Typography>
                                  {product.content}
                                </Typography>
                              </CardContent>
                              <CardActions>
                                <Link to={`/product/`+product.id}>
                                  <Button variant="contained" size="small" color="primary">
                                    View
                                  </Button>
                                </Link>
                                <Button variant="contained" size="small" color="primary">
                                  Edit
                                </Button>
                              </CardActions>
                            </Card>
                          </Grid> 
                        )}
                    </Grid>
                </div>
            </main>
        </div>
        </React.Fragment>
    )
}

export const ProductView = ({ match }) => {
    const product = products.find(item => item.id === match.params.id);
    console.log(products, match.params.id);
    return(
        <React.Fragment>
            <div style={{ marginRight: '5%', marginLeft: '5%' }}>
                <h3> Detail Produk </h3>
                <main>
                    <div>
                        <div>
                            <Link to={`/catalog`}> Back to Catalog </Link>
                        </div>
                        {/* End hero unit */}
                        <Grid container spacing={40}>
                            <Grid item key={product.id} sm={6} md={4} lg={12}>
                              <Card>
                                <CardMedia
                                  style={{ paddingTop: '50%' }}
                                  image={product.image}
                                  title="Image title"
                                />
                                <CardContent>
                                  <Typography gutterBottom variant="h5" component="h2">
                                    {product.label}
                                  </Typography>
                                  <Typography>
                                    {product.content}
                                  </Typography>
                                </CardContent>
                              </Card>
                            </Grid> 
                        </Grid>
                    </div>
                </main>
            </div>
        </React.Fragment>
    )
}