import React from 'react';
import {Link, Route } from 'react-router-dom';

const NestedView = ({ match }) => {
    return(
        <div>
            <Route exact path={match.url} />
            <h3> Silakan Pilih : </h3>
            <ul>
                <li> <Link to={`${match.url}/info`}> Info </Link> </li>
                <li> <Link to={`${match.url}/about`}> About </Link> </li>
                <li> <Link to={`${match.url}/contacs`}> Contact </Link> </li>
            </ul>

            <Route path={`${match.url}/:sectionName`} component={SubView} />
        </div>
    )
};

const SubView = ({ match }) => {
    return(
        <div>
            <h3> Section : {match.params.sectionName} </h3>
        </div>
    )
};

export default NestedView;