import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { CatalogView, ProductView } from './views/Catalog';
import NestedView from './views/Nested';
import WarungView from './views/Warung';
import Nav from './shared/Navbar';
import Footer from './shared/Footer';

function NotFound() {
    return(
        <div>
            <h1> 404 Not Found </h1>
            <p> <h3> The Page What Your Request Is Not Found </h3> </p>
        </div>
    );
};

class Indes extends Component {
    render() {
        return(
            <BrowserRouter>
                <div>    
                    <Nav />
                        <Switch>
                            <Route path="/" exact component={CatalogView} />
                            <Route path="/warung" component={WarungView} />
                            <Route path="/catalog" component={CatalogView} />
                            <Route path="/product/:id" component={ProductView} />
                            <Route path="/nested" component={NestedView} />
                            <Route component={NotFound} /> 
                        </Switch>
                    <Footer />
                </div>
            </BrowserRouter>
        );
    }
}

export default Indes;