import React, { Component } from 'react';
import { Container, Row, Col, Form, Button, Card } from 'react-bootstrap';

class Warung extends Component {
    render() {
        return(
            <React.Fragment>
                <div>
                    <Container>
                        <Row justify="center">
                            <Card style={{ width: '100%' }}>
                                <Card.Body>
                                    <Col sm={12}>
                                        <Form fullWidth>
                                          <Form.Group controlId="exampleForm.ControlInput1">
                                              <Form.Label> Nama Warung </Form.Label>
                                              <Form.Control type="text" placeholder="Input Kode Warung" />
                                            </Form.Group>
                                            <Form.Group controlId="exampleForm.ControlSelect1">
                                              <Form.Label> Kode Warung </Form.Label>
                                              <Form.Control as="select">
                                                  <option> -- Select -- </option>
                                                  <option> 1 </option>
                                                  <option> 2 </option>
                                                  <option> 3 </option>
                                                  <option> 4 </option>
                                                  <option> 5 </option>
                                              </Form.Control>
                                            </Form.Group>
                                            <Form.Group controlId="exampleForm.ControlTextarea1">
                                              <Form.Label> Alamat </Form.Label>
                                              <Form.Control as="textarea" rows="3" />
                                            </Form.Group>
                                            <Form.Group controlId="exampleForm.ControlInput1">
                                              <Form.Label> No. Telp </Form.Label>
                                              <Form.Control type="text" placeholder="Input Kode Warung" />
                                            </Form.Group>
                                            <Form.Group controlId="exampleForm.ControlTextarea1">
                                              <Form.Label> Deskripsi </Form.Label>
                                              <Form.Control as="textarea" rows="3" />
                                            </Form.Group>
                                            <Form.Group controlId="exampleForm.ControlInput1">
                                              <Form.Label> Upload Foto </Form.Label>
                                              <Form.Control type="file" />
                                            </Form.Group>
                                            <Button variant="primary" type="submit">
                                              Submit
                                            </Button>
                                        </Form>
                                    </Col>
                                </Card.Body>
                            </Card>
                        </Row>
                    </Container>
                </div>    
            </React.Fragment>
        );
    }
}

export default Warung;